<template>
    <v-scroll-view class="demo-page">
        <v-cell @click="$toast('3')">, 3</v-cell>
        <v-cell @click="$toast('3', {type:'success'})"><a class="text-success"></a>, 3</v-cell>
        <v-cell @click="$toast('3', {type:'warning'})"><a class="text-warning"></a>, 3</v-cell>
        <v-cell @click="$toast('3', {type:'danger'})"><a class="text-danger"></a>, 3</v-cell>
        <v-cell @click="$toast('3', {type:'loading'})">, 3</v-cell>
    </v-scroll-view>
</template>
<script>
import VScrollView from '@/packages/ScrollView/ScrollView';
import VCell from '@/packages/Cell/Cell';
import VButton from '@/packages/Button/Button';
export default {
    name: 'FingerDemo',

    data() {
        return {
            activeIndex: 0
        };
    },


    components: {
        VCell,
        VScrollView
    }
};
</script>
<style scoped lang="scss">
@import '../scss/variables.scss';
.demo-page {
    height: calc(100% - 55px);
}
</style>



