<script>
import AMask from '../../packages/Mask';
import AIcon from '../../packages/Icon';
import ALoading from '../../packages/Loading';
export default {
    name: 'AtomToast',

    components: {
        AMask,
        ALoading,
        AIcon,
    },

    props: {
        // 
        isShow: {
            type: Boolean,
            default: false,
        },

        // , vnode
        content: {
            type: [String, Object],
        },

        // , 0
        delay: {
            type: Number,
            default: 3000,
        },

        // 
        position: {
            type: String,
            default: 'center',
        },

        // , ///loading
        type: {
            type: String,
            default: 'danger',
        },

        // mask
        closeable: {
            type: Boolean,
            default: true,
        },
    },

    data() {
        return { isShowMask: true, timeoutId: null };
    },

    watch: {
        isShow(value) {
            clearTimeout(this.timeoutId);
            if (value) {
                this.isShowMask = value;
                if (0 < this.delay) {
                    this.timeoutId = setTimeout(() => {
                        this.isShowMask = false;
                        this.$emit('update:isShow', false);
                    }, this.delay);
                }
            }
        },
    },

    methods: {
        afterLeave() {
            this.isShowMask = false;
        },

        /**
         * 
         */
        closeAll(e) {
            if (this.closeable) {
                clearTimeout(this.timeoutId);
                this.isShowMask = false;
                this.$emit('update:isShow', false);
            }
        },
    },

    render(h) {
        // type
        let map = {
            success: 'tick',
            warning: 'warning',
            danger: 'close',
        };

        if (this.isShowMask) {
            let vnodeIcon;
            if ('loading' === this.type) {
                vnodeIcon = h('a-loading', {
                    props: { type: 'primary' },
                    class: ['gutter-auto', 'gutter-bottom-sm'],
                    style: { display: 'block' },
                });
            } else {
                vnodeIcon = h('a-icon', {
                    props: {
                        name: map[this.type],
                    },
                    class: ['gutter-auto'],
                    style: { display: 'block' },
                });
            }

            return h(
                'a-mask',
                {
                    props: {
                        isShow: true,
                        closeable: this.closeable,
                    },
                    style: {
                        background: 'rgba(255,255,255, 0)',
                    },
                    on: {
                        'update:isShow': this.closeAll,
                    },
                },
                [
                    this.isShow &&
                        h(
                            'transition',
                            {
                                attrs: { name: 'fade' },
                                on: { 'after-leave': this.afterLeave },
                            },
                            [
                                h(
                                    'div',
                                    { class: [this.position, 'atom-toast', `text-${this.type}`] },
                                    // vnode, 
                                    ['default' !== this.type && vnodeIcon, this.content._compiled ? h(this.content) : this.content],
                                ),
                            ],
                        ),
                ],
            );
        }
    },
};
</script>


