<template>
    <main class="fill">
        <a-button @click="open">this.$loading(), 3</a-button>
    </main>
</template>
<script>
export default {
    name: 'ApiLoadingDemo',

    methods: {
        open() {
            this.$loading('');
            setTimeout(() => {
                this.$loading.close();
            }, 3000);
        },
    },
};
</script>


